.file-msg {
  position: relative;
  cursor: pointer;
}
.file-msg .pic {
  border-radius: 4px;
  overflow: hidden;
}
.file-msg .pic img {
  max-width: 300px;
  max-height: 300px;
}
.file-msg .pic .hide {
  display: none;
}
.file-msg .file {
  width: 240px;
  height: 90px;
  display: flex;
  position: relative;
  background: #fff;
  border: 1px solid rgba(60, 64, 67, 0.3);
  border-radius: 4px;
}
.file-msg .file .file-left {
  width: 90px;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.file-msg .file .file-left .file-icon {
  width: 76px;
  height: 76px;
}
.file-msg .file .file-right {
  flex: 1 1;
  max-width: 150px;
  padding: 10px 10px 10px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  padding: 10px;
}
.file-msg .file .file-right .file-card-name {
  height: 32px;
  font-size: 14px;
  line-height: 16px;
  white-space: nowrap;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
}
.file-msg .file .file-right .file-card-size {
  text-align: left;
}
.file-msg .file:hover {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  background: rgba(255, 255, 255, 0.6);
}
