.chat {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.chat .main {
  background: #fff;
  width: 900px;
  height: 750px;
  position: relative;
  margin: 0 auto;
  border-radius: 4px;
  box-shadow: rgba(60, 64, 67, 0.3) 0 1px 2px 0, rgba(60, 64, 67, 0.15) 0 1px 3px 1px;
  display: flex;
}
.chat .main .left {
  width: 280px;
  display: flex;
  flex-direction: column;
  background: #2e3238;
}
.chat .main .left .actions {
  position: relative;
}
.chat .main .left .actions .search {
  position: relative;
  width: 244px;
  margin: 0 auto;
}
.chat .main .left .actions .search input {
  outline: none;
  border: none;
  background: none;
  width: 100%;
  height: 32px;
  line-height: 32px;
  border: 0;
  border-radius: 2px;
  color: #fff;
  padding-left: 30px;
  font-size: 12px;
  background-color: #26292e;
}
.chat .main .left .actions .search .icon {
  position: absolute;
  left: 0;
  top: 1px;
  width: 30px;
  height: 30px;
}
.chat .main .left .actions .tabs {
  margin-top: 6px;
  width: 100%;
}
.chat .main .left .actions .tabs .tabs-item {
  position: relative;
  display: inline-block;
  width: 50%;
  text-align: center;
  cursor: pointer;
}
.chat .main .left .actions .tabs .tabs-item i {
  display: inline-block;
  width: 35px;
  height: 35px;
}
.chat .main .left .actions .tabs .tabs-item::after {
  content: '';
  position: absolute;
  top: 7px;
  right: 0;
  width: 0;
  height: 20px;
  border-right: 1px solid #24272c;
}
.chat .main .left .actions .tabs::after {
  content: '';
  position: absolute;
  border-bottom: 1px solid #24272c;
  height: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.chat .main .left .actions::after {
  content: '';
  position: absolute;
  top: 7px;
  right: 0;
  width: 0;
  height: 20px;
  border-right: 1px solid #24272c;
}
.chat .main .left .user-list {
  height: calc(100% - 67px);
  position: relative;
}
.chat .main .left .user-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
  color: #fff;
  height: 100%;
  overflow: hidden;
  overflow-y: auto;
  /*定义滚动条轨道 内阴影+圆角*/
  /*定义滑块 内阴影+圆角*/
}
.chat .main .left .user-list ul::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
.chat .main .left .user-list ul::-webkit-scrollbar-track {
  display: none;
}
.chat .main .left .user-list ul::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.2);
}
.chat .main .left .user-list ul li {
  padding: 10px 20px;
  border-bottom: 1px solid #292c33;
  cursor: pointer;
  position: relative;
}
.chat .main .left .user-list ul li .warp {
  width: 100%;
  display: flex;
  align-items: center;
}
.chat .main .left .user-list ul li .warp .info {
  flex: 1 1;
}
.chat .main .left .user-list ul li .warp .info .text {
  display: block;
  margin-left: 15px;
  max-width: 130px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: left;
  font-size: 13px;
}
.chat .main .left .user-list ul li .warp .info .msg {
  font-size: 12px;
  color: #6b6f7c;
  margin-top: 3px;
}
.chat .main .left .user-list ul li .warp .time {
  color: #6b6f7c;
}
.chat .main .left .session-list ul li .warp {
  align-items: unset;
}
.chat .main .left .session-list ul .list-active {
  background: #3a3f45;
}
.chat .main .left .session-list ul .list-active .warp .info .msg {
  color: #AAA;
}
.chat .main .left .session-list ul .list-active .warp .time {
  color: #fff;
}
.chat .main .right {
  flex: 1 1;
  position: relative;
  background: #eee;
}
.chat .main .right .main-msg-header {
  width: 100%;
  height: 6%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.chat .main .right .main-msg-send {
  height: 24%;
  position: relative;
  border-top: 1px solid #ddd;
  display: flex;
  flex-direction: column;
}
.chat .main .right .main-msg-send .toolbar {
  width: 100%;
  display: flex;
  padding: 10px 20px 5px 20px;
}
.chat .main .right .main-msg-send .toolbar .toolbar-btn {
  cursor: pointer;
  font-size: 20px;
}
.chat .main .right .main-msg-send .main-msg-input {
  display: flex;
  width: 100%;
  height: 48%;
  position: relative;
}
.chat .main .right .main-msg-send .main-msg-input textarea {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  resize: none;
  padding-left: 20px;
  background: transparent;
}
.chat .main .right .main-msg-send .send-btn {
  flex: 1 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 20px;
}
