ul,
li {
  list-style: none;
}
.home {
  width: 1000px;
  margin: 0 auto;
  position: relative;
  box-sizing: border-box;
  height: calc(100vh - 60px);
  padding-top: 20px;
}
.home .file-body {
  background: #fff;
  position: relative;
  padding: 8px 0;
  border-radius: 4px;
  box-shadow: rgba(60, 64, 67, 0.3) 0 1px 2px 0, rgba(60, 64, 67, 0.15) 0 1px 3px 1px;
}
.home .empty {
  padding-top: 200px;
  height: 600px;
}
.upload {
  position: absolute;
  bottom: 20px;
  right: 20px;
}
@media screen and (max-width: 1400px) {
  .home {
    width: 800px;
  }
}
