.main-msg {
  height: 70%;
}
.main-msg .scroll-warp {
  width: 100%;
  height: 100%;
  overflow-y: hidden;
  overflow-x: hidden;
  text-align: left;
  /*定义滚动条轨道 内阴影+圆角*/
  /*定义滑块 内阴影+圆角*/
}
.main-msg .scroll-warp:hover {
  overflow-y: auto;
}
.main-msg .scroll-warp::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
.main-msg .scroll-warp::-webkit-scrollbar-track {
  display: none;
}
.main-msg .scroll-warp::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.2);
}
.main-msg .scroll-warp .msg-list {
  position: relative;
  display: inline-block;
  width: 610px;
  padding: 10px 10px 10px 20px;
}
.main-msg .scroll-warp .msg-list .msg-list-item {
  width: 100%;
  display: flex;
  padding: 12px 0;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content {
  position: relative;
  max-width: 70%;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .username {
  margin: 0 10px;
  margin-top: -10px;
  text-align: left;
  font-size: 12px;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .bubble {
  width: auto;
  margin: 0 10px;
  position: relative;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .bubble::after {
  content: '';
  position: absolute;
  border-width: 6px 5px 6px 5px;
  border-style: solid;
  border-color: transparent #fff transparent transparent;
  left: -10px;
  top: 7px;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .bubble span {
  line-height: 20px;
  display: inline-block;
  word-wrap: break-word;
  text-align: left;
  max-width: 100%;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .bubble .loading {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .bubble .text-card {
  padding: 10px 15px;
  background: #fff;
  border-radius: 4px;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .gray::after {
  border-color: transparent rgba(60, 64, 67, 0.3) transparent transparent;
}
.main-msg .scroll-warp .msg-list .right {
  flex-direction: row-reverse;
}
.main-msg .scroll-warp .msg-list .right .content .username {
  text-align: right;
}
.main-msg .scroll-warp .msg-list .right .content .bubble .text-card {
  color: #fff;
  background: rgba(60, 64, 67, 0.3);
}
.main-msg .scroll-warp .msg-list .right .content .bubble::after {
  border-color: transparent transparent transparent rgba(60, 64, 67, 0.3);
  left: auto;
  right: -10px;
}
.main-msg .scroll-warp .msg-list .hide {
  overflow: hidden;
}
.big-pic {
  text-align: center!important;
}
.big-pic .img-preview {
  width: auto;
}
.big-pic .img-preview img {
  max-width: 1000px;
  max-height: 700px;
}
.big-pic .ant-modal-content {
  display: inline-block;
}
.big-pic .ant-modal-content .ant-modal-body {
  padding: 10px;
}
.big-pic .ant-modal-footer {
  display: none;
}
