.file-list {
  position: relative;
}
.file-list ul {
  padding: 0;
  margin: 0;
}
.file-list ul li {
  text-align: left;
  padding-left: 20px;
  width: 100%;
  box-sizing: border-box;
  height: 40px;
  display: flex;
  flex-direction: row;
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  align-items: center;
  font-size: 13px;
}
.file-list ul li .icon {
  width: 16px;
  height: 16px;
  background: #ccc;
}
.file-list ul li .filename {
  color: #202124;
  margin-left: 20px;
  width: 25%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.file-list ul li .time {
  margin-left: 20px;
  color: rgba(0, 0, 0, 0.54);
}
.file-list ul li .size {
  display: none;
  color: rgba(0, 0, 0, 0.54);
  margin-left: 20px;
  max-width: 100px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.file-list ul li .operation {
  margin-left: 20px;
  flex: 1 1;
  padding-right: 20px;
  display: flex;
  justify-content: flex-end;
}
.file-list ul li .operation div {
  margin-left: 10px;
  cursor: pointer;
}
.file-list ul li .operation div:hover {
  color: #3367d6;
}
.file-list ul li:hover {
  background: #e8f0fe;
}
.file-list ul li:hover .size {
  display: block;
}
