body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  position: relative;
  height: 100vh;
  background: #f8f9fa;
}
.App .header {
  position: relative;
  height: 60px;
  background: #3367d6;
  color: #fff;
}
.wechat_search {
  background: url(/static/media/wechat.5af37c4a.png);
  background-position: -60px -432px;
  background-size: 487px 462px;
}
.wechat_chat_active {
  background: url(/static/media/wechat.5af37c4a.png);
  background-position: -185px -96px;
  background-size: 487px 462px;
}
.wechat_chat {
  background: url(/static/media/wechat.5af37c4a.png);
  background-position: -150px -96px;
  background-size: 487px 462px;
}
.wechat_friends {
  background: url(/static/media/wechat.5af37c4a.png);
  background-position: -220px -96px;
  background-size: 487px 462px;
}
.wechat_friends_active {
  background: url(/static/media/wechat.5af37c4a.png);
  background-position: -304px -246px;
  background-size: 487px 462px;
}
.wechat_unkown_file {
  background: url(/static/media/wechat.5af37c4a.png) no-repeat;
  background-position: -346px -152px;
  background-size: 487px 462px;
}
.wechat_text {
  background: url(/static/media/wechat.5af37c4a.png) no-repeat;
  background-position: 0 -322px;
  background-size: 487px 462px;
}

.file-list {
  position: relative;
}
.file-list ul {
  padding: 0;
  margin: 0;
}
.file-list ul li {
  text-align: left;
  padding-left: 20px;
  width: 100%;
  box-sizing: border-box;
  height: 40px;
  display: flex;
  flex-direction: row;
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  align-items: center;
  font-size: 13px;
}
.file-list ul li .icon {
  width: 16px;
  height: 16px;
  background: #ccc;
}
.file-list ul li .filename {
  color: #202124;
  margin-left: 20px;
  width: 25%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.file-list ul li .time {
  margin-left: 20px;
  color: rgba(0, 0, 0, 0.54);
}
.file-list ul li .size {
  display: none;
  color: rgba(0, 0, 0, 0.54);
  margin-left: 20px;
  max-width: 100px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.file-list ul li .operation {
  margin-left: 20px;
  flex: 1 1;
  padding-right: 20px;
  display: flex;
  justify-content: flex-end;
}
.file-list ul li .operation div {
  margin-left: 10px;
  cursor: pointer;
}
.file-list ul li .operation div:hover {
  color: #3367d6;
}
.file-list ul li:hover {
  background: #e8f0fe;
}
.file-list ul li:hover .size {
  display: block;
}

.tool-bar {
  position: absolute;
  top: 0;
  right: -40px;
}
.tool-bar .tool-list {
  display: flex;
  flex-direction: column;
}
.tool-bar .tool-list .tool-btn {
  margin-top: 10px;
}
.tool-bar .sort {
  position: relative;
}

ul,
li {
  list-style: none;
}
.home {
  width: 1000px;
  margin: 0 auto;
  position: relative;
  box-sizing: border-box;
  height: calc(100vh - 60px);
  padding-top: 20px;
}
.home .file-body {
  background: #fff;
  position: relative;
  padding: 8px 0;
  border-radius: 4px;
  box-shadow: rgba(60, 64, 67, 0.3) 0 1px 2px 0, rgba(60, 64, 67, 0.15) 0 1px 3px 1px;
}
.home .empty {
  padding-top: 200px;
  height: 600px;
}
.upload {
  position: absolute;
  bottom: 20px;
  right: 20px;
}
@media screen and (max-width: 1400px) {
  .home {
    width: 800px;
  }
}

.user-avatar {
  width: 40px;
  height: 40px;
}
.user-avatar .user-avatar-content {
  width: 40px;
  height: 40px;
  border-radius: 4px;
  background: #fff;
  cursor: pointer;
}

.file-msg {
  position: relative;
  cursor: pointer;
}
.file-msg .pic {
  border-radius: 4px;
  overflow: hidden;
}
.file-msg .pic img {
  max-width: 300px;
  max-height: 300px;
}
.file-msg .pic .hide {
  display: none;
}
.file-msg .file {
  width: 240px;
  height: 90px;
  display: flex;
  position: relative;
  background: #fff;
  border: 1px solid rgba(60, 64, 67, 0.3);
  border-radius: 4px;
}
.file-msg .file .file-left {
  width: 90px;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.file-msg .file .file-left .file-icon {
  width: 76px;
  height: 76px;
}
.file-msg .file .file-right {
  flex: 1 1;
  max-width: 150px;
  padding: 10px 10px 10px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  padding: 10px;
}
.file-msg .file .file-right .file-card-name {
  height: 32px;
  font-size: 14px;
  line-height: 16px;
  white-space: nowrap;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
}
.file-msg .file .file-right .file-card-size {
  text-align: left;
}
.file-msg .file:hover {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  background: rgba(255, 255, 255, 0.6);
}

.main-msg {
  height: 70%;
}
.main-msg .scroll-warp {
  width: 100%;
  height: 100%;
  overflow-y: hidden;
  overflow-x: hidden;
  text-align: left;
  /*定义滚动条轨道 内阴影+圆角*/
  /*定义滑块 内阴影+圆角*/
}
.main-msg .scroll-warp:hover {
  overflow-y: auto;
}
.main-msg .scroll-warp::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
.main-msg .scroll-warp::-webkit-scrollbar-track {
  display: none;
}
.main-msg .scroll-warp::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.2);
}
.main-msg .scroll-warp .msg-list {
  position: relative;
  display: inline-block;
  width: 610px;
  padding: 10px 10px 10px 20px;
}
.main-msg .scroll-warp .msg-list .msg-list-item {
  width: 100%;
  display: flex;
  padding: 12px 0;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content {
  position: relative;
  max-width: 70%;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .username {
  margin: 0 10px;
  margin-top: -10px;
  text-align: left;
  font-size: 12px;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .bubble {
  width: auto;
  margin: 0 10px;
  position: relative;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .bubble::after {
  content: '';
  position: absolute;
  border-width: 6px 5px 6px 5px;
  border-style: solid;
  border-color: transparent #fff transparent transparent;
  left: -10px;
  top: 7px;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .bubble span {
  line-height: 20px;
  display: inline-block;
  word-wrap: break-word;
  text-align: left;
  max-width: 100%;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .bubble .loading {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .bubble .text-card {
  padding: 10px 15px;
  background: #fff;
  border-radius: 4px;
}
.main-msg .scroll-warp .msg-list .msg-list-item .content .gray::after {
  border-color: transparent rgba(60, 64, 67, 0.3) transparent transparent;
}
.main-msg .scroll-warp .msg-list .right {
  flex-direction: row-reverse;
}
.main-msg .scroll-warp .msg-list .right .content .username {
  text-align: right;
}
.main-msg .scroll-warp .msg-list .right .content .bubble .text-card {
  color: #fff;
  background: rgba(60, 64, 67, 0.3);
}
.main-msg .scroll-warp .msg-list .right .content .bubble::after {
  border-color: transparent transparent transparent rgba(60, 64, 67, 0.3);
  left: auto;
  right: -10px;
}
.main-msg .scroll-warp .msg-list .hide {
  overflow: hidden;
}
.big-pic {
  text-align: center!important;
}
.big-pic .img-preview {
  width: auto;
}
.big-pic .img-preview img {
  max-width: 1000px;
  max-height: 700px;
}
.big-pic .ant-modal-content {
  display: inline-block;
}
.big-pic .ant-modal-content .ant-modal-body {
  padding: 10px;
}
.big-pic .ant-modal-footer {
  display: none;
}

.user {
  border-radius: 4px 0 0 0;
  position: relative;
  display: flex;
  padding: 10px 20px;
  align-items: center;
}
.user .user-info {
  padding: 0 15px;
}
.user .user-info .user-item {
  text-align: left;
  position: relative;
}
.user .user-info .user-item .user-name-input {
  width: calc(100% - 20px);
  left: 0;
  font-weight: 500;
  font-size: 16px;
  background: transparent;
  outline: none;
  border: none;
  color: #fff;
  vertical-align: baseline;
}
.user .user-info .user-item span {
  font-size: 12px;
  color: #999;
}
.user .avatar-list {
  position: absolute;
  top: 100%;
  width: 416px;
  background: #fff;
  z-index: 999;
  border-radius: 4px;
  box-shadow: rgba(60, 64, 67, 0.3) 0 1px 2px 0, rgba(60, 64, 67, 0.15) 0 1px 3px 1px;
  padding: 10px;
  text-align: left;
  overflow: hidden;
  overflow-y: auto;
}
.user .avatar-list .avatar-item {
  position: relative;
  display: inline-block;
  width: 46px;
  height: 46px;
  margin: 10px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border: 1px solid transparent;
  border-radius: 4px;
  line-height: 46px;
  text-align: center;
  background: #e0e0e0;
  box-sizing: border-box;
}
.user .avatar-list .avatar-item img {
  width: 80%;
  height: 80%;
}
.user .avatar-list .avatar-item:hover {
  cursor: pointer;
  border: 1px solid rgba(60, 64, 67, 0.3);
}
.user .avatar-list .active::after {
  content: '';
  position: absolute;
  top: -1px;
  left: -1px;
  display: block;
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  border-radius: 4px;
  border: 2px solid #096dd9;
}

.chat {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.chat .main {
  background: #fff;
  width: 900px;
  height: 750px;
  position: relative;
  margin: 0 auto;
  border-radius: 4px;
  box-shadow: rgba(60, 64, 67, 0.3) 0 1px 2px 0, rgba(60, 64, 67, 0.15) 0 1px 3px 1px;
  display: flex;
}
.chat .main .left {
  width: 280px;
  display: flex;
  flex-direction: column;
  background: #2e3238;
}
.chat .main .left .actions {
  position: relative;
}
.chat .main .left .actions .search {
  position: relative;
  width: 244px;
  margin: 0 auto;
}
.chat .main .left .actions .search input {
  outline: none;
  border: none;
  background: none;
  width: 100%;
  height: 32px;
  line-height: 32px;
  border: 0;
  border-radius: 2px;
  color: #fff;
  padding-left: 30px;
  font-size: 12px;
  background-color: #26292e;
}
.chat .main .left .actions .search .icon {
  position: absolute;
  left: 0;
  top: 1px;
  width: 30px;
  height: 30px;
}
.chat .main .left .actions .tabs {
  margin-top: 6px;
  width: 100%;
}
.chat .main .left .actions .tabs .tabs-item {
  position: relative;
  display: inline-block;
  width: 50%;
  text-align: center;
  cursor: pointer;
}
.chat .main .left .actions .tabs .tabs-item i {
  display: inline-block;
  width: 35px;
  height: 35px;
}
.chat .main .left .actions .tabs .tabs-item::after {
  content: '';
  position: absolute;
  top: 7px;
  right: 0;
  width: 0;
  height: 20px;
  border-right: 1px solid #24272c;
}
.chat .main .left .actions .tabs::after {
  content: '';
  position: absolute;
  border-bottom: 1px solid #24272c;
  height: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.chat .main .left .actions::after {
  content: '';
  position: absolute;
  top: 7px;
  right: 0;
  width: 0;
  height: 20px;
  border-right: 1px solid #24272c;
}
.chat .main .left .user-list {
  height: calc(100% - 67px);
  position: relative;
}
.chat .main .left .user-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
  color: #fff;
  height: 100%;
  overflow: hidden;
  overflow-y: auto;
  /*定义滚动条轨道 内阴影+圆角*/
  /*定义滑块 内阴影+圆角*/
}
.chat .main .left .user-list ul::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
.chat .main .left .user-list ul::-webkit-scrollbar-track {
  display: none;
}
.chat .main .left .user-list ul::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.2);
}
.chat .main .left .user-list ul li {
  padding: 10px 20px;
  border-bottom: 1px solid #292c33;
  cursor: pointer;
  position: relative;
}
.chat .main .left .user-list ul li .warp {
  width: 100%;
  display: flex;
  align-items: center;
}
.chat .main .left .user-list ul li .warp .info {
  flex: 1 1;
}
.chat .main .left .user-list ul li .warp .info .text {
  display: block;
  margin-left: 15px;
  max-width: 130px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: left;
  font-size: 13px;
}
.chat .main .left .user-list ul li .warp .info .msg {
  font-size: 12px;
  color: #6b6f7c;
  margin-top: 3px;
}
.chat .main .left .user-list ul li .warp .time {
  color: #6b6f7c;
}
.chat .main .left .session-list ul li .warp {
  align-items: unset;
}
.chat .main .left .session-list ul .list-active {
  background: #3a3f45;
}
.chat .main .left .session-list ul .list-active .warp .info .msg {
  color: #AAA;
}
.chat .main .left .session-list ul .list-active .warp .time {
  color: #fff;
}
.chat .main .right {
  flex: 1 1;
  position: relative;
  background: #eee;
}
.chat .main .right .main-msg-header {
  width: 100%;
  height: 6%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.chat .main .right .main-msg-send {
  height: 24%;
  position: relative;
  border-top: 1px solid #ddd;
  display: flex;
  flex-direction: column;
}
.chat .main .right .main-msg-send .toolbar {
  width: 100%;
  display: flex;
  padding: 10px 20px 5px 20px;
}
.chat .main .right .main-msg-send .toolbar .toolbar-btn {
  cursor: pointer;
  font-size: 20px;
}
.chat .main .right .main-msg-send .main-msg-input {
  display: flex;
  width: 100%;
  height: 48%;
  position: relative;
}
.chat .main .right .main-msg-send .main-msg-input textarea {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  resize: none;
  padding-left: 20px;
  background: transparent;
}
.chat .main .right .main-msg-send .send-btn {
  flex: 1 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 20px;
}

