.user {
  border-radius: 4px 0 0 0;
  position: relative;
  display: flex;
  padding: 10px 20px;
  align-items: center;
}
.user .user-info {
  padding: 0 15px;
}
.user .user-info .user-item {
  text-align: left;
  position: relative;
}
.user .user-info .user-item .user-name-input {
  width: calc(100% - 20px);
  left: 0;
  font-weight: 500;
  font-size: 16px;
  background: transparent;
  outline: none;
  border: none;
  color: #fff;
  vertical-align: baseline;
}
.user .user-info .user-item span {
  font-size: 12px;
  color: #999;
}
.user .avatar-list {
  position: absolute;
  top: 100%;
  width: 416px;
  background: #fff;
  z-index: 999;
  border-radius: 4px;
  box-shadow: rgba(60, 64, 67, 0.3) 0 1px 2px 0, rgba(60, 64, 67, 0.15) 0 1px 3px 1px;
  padding: 10px;
  text-align: left;
  overflow: hidden;
  overflow-y: auto;
}
.user .avatar-list .avatar-item {
  position: relative;
  display: inline-block;
  width: 46px;
  height: 46px;
  margin: 10px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border: 1px solid transparent;
  border-radius: 4px;
  line-height: 46px;
  text-align: center;
  background: #e0e0e0;
  box-sizing: border-box;
}
.user .avatar-list .avatar-item img {
  width: 80%;
  height: 80%;
}
.user .avatar-list .avatar-item:hover {
  cursor: pointer;
  border: 1px solid rgba(60, 64, 67, 0.3);
}
.user .avatar-list .active::after {
  content: '';
  position: absolute;
  top: -1px;
  left: -1px;
  display: block;
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  border-radius: 4px;
  border: 2px solid #096dd9;
}
